.App {
  text-align: center;
}

.headerLogo {
  display: inline-block;
  margin-left: 1em;
  margin-right: 1em;
  margin-top: 2em;
  margin-bottom: 1em;
  max-width:90%;
  max-height:90%;
  position: relative;
  padding: .5rem;
}


.navbar{
  border: 4px solid black;
  width: 100%;
  height: 40px;
  position: fixed;
  top: 0; /* Position the navbar at the top of the page */
  background-color:black;
  z-index: 4;
}


body {
  height: 100%;
  background-image: url('./components/kitchen.jpg');
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.text{
  color:white;
  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
  margin-right: 30rem;

}
.text-background{
  background-color: rgba(0,0,0,.5);
  width: 100%;
  padding: 3rem;
  height: 20rem;
  position: relative;
  top: 20rem;


}

.window2{
  background-color: rgba(0,0,0,1);
  width: 100%;
  padding: 3rem;
  height: 100%;
  position: relative;
  top:18rem;


}
.row{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}
.column {
  padding: 5rem;
  justify-content: center;

}

.portImage{
  width: 30rem;
}
@media only screen and (max-width: 700px){
  .row{
    flex-direction: column;
  }
  .portImage{
    max-width: 95%;
    max-height: 95%
  }


  .text{
    margin-right: 5rem;
  }


}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


